
@charset "UTF-8";
#app {
  font-family: Helvetica, Arial, '\5FAE\8EDF\6B63\9ED1\9AD4', Microsoft JhengHei, Apple LiGothic, '\860B\679C\5137\4E2D\9ED1';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  line-height: 1.5;
}
#app img.tltImg {
    width: 60%;
    max-width: 250px;
}
#app .flexBox {
    max-width: 800px;
    margin: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
#app .flexBox .btn {
      width: 23%;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      border-radius: 50px;
      border: 1px solid rgba(255, 255, 255, 0.5);
      text-decoration: none;
      font-size: 16px;
      line-height: 2.5;
      font-weight: 600;
      background-color: rgba(245, 182, 65, 0.8);
      color: #ffffff;
      -webkit-transition: 0.2s;
      transition: 0.2s;
}
#app .flexBox .btn:hover {
        background-color: #f5b641;
        -webkit-transform: translate(5px, 5px);
                transform: translate(5px, 5px);
        color: #000000;
}
#app .page-enter-active, #app .page-leave-active {
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
#app .page-enter, #app .page-leave-to {
    opacity: 0;
}
#app .homeImg {
    width: 40px;
}
#app h1 {
    color: #faf7be;
    margin-bottom: 0;
}
#app h3 {
    color: #ffffff;
    font-weight: normal;
    font-size: 18px;
    letter-spacing: 0.08em;
}
#app h4 {
    color: rgba(255, 255, 255, 0.5);
    font-weight: normal;
    font-size: 15px;
    letter-spacing: 0.08em;
    margin-top: 0;
}
@media screen and (max-width: 540px) {
#app .flexBox {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
#app .flexBox .btn {
      width: 46%;
      font-size: 16px;
      margin-bottom: 20px;
}
}

